@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

body {
  overscroll-behavior-y: none;
  background-color: #ffffff;
  color: rgb(51, 51, 51);
}

.App {
  height: 100%;
  width: 100%;
}

* {
  margin: 0;
  color: #37393a;
  font-family: 'Poppins', sans-serif;
  font-size: 400;
}

.versionNumber {
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 0 2vh 5vh 0%;
  z-index: 10;
}
